import Hatim from '../images/Hatim Dahod.mp4'
import Ahmed from '../images/Ahmed Vadnagar.mp4'
import Abduttayeb from '../images/Abduttayeb Kanchwala.mp4'
import murtaza from '../images/murtaza Bandeali.mp4'
import Aliasger from '../images/Aliasger Kanchwala.mp4'
import Mohammed from '../images/Mohammed Bhiwandiwala.mp4'
import Abbas from '../images/Abbas Mamka.mp4'
import Surprise from '../images/Surprise.mp4'

var cakeImages = {Hussain,Murtaza,Hatim,Ahmed,Abduttayeb,murtaza,Aliasger,Mohammed,Abbas,Surprise}

class Card extends React.Component{
    handleClick(t){
        t.preventDefault()
        //window.location.href="https://www.instagram.com/frozen_cakes_/"
    }
    render(){
        // const cardBg="red";
        const cardBg=`white`;
        return (
            <div className="card-wrapper">
                <div className="card" style={{background:cardBg}}>
                    {/* <img className="cake" src={}/> */}
                    <video style={{width:"60vw"}} controls src={cakeImages[this.props.vsrc.split(" ")[0]]}></video>
                    {/* <div className="card-name">{this.props.vsrc}</div> */}
                    {/* <div className="card-desc">{this.props.data.description}</div> */}
                    {/* <div>
                        <button className="buy" onClick={this.handleClick}>Buy Now</button>
                    </div> */}
                </div>
            </div>
        )
    }
}

export default Card