import React from "react"
import '../App.css';

class CategoryName extends React.Component{
    getStyle(name) {
        if(name=="Surprise "){
            return {
                background: "linear-gradient(to right,rgba(255,0,0,0.3),rgba(0,0,255,0.3))",
                color:'#FFF',
                fontWeight:'bold',
                padding:"15px",
                letterSpacing:'2px'
            }
        }
        if(name == this.props.vsrc) {
            return {
                backgroundColor:"#3498db",
                color:'#FFF',
                fontWeight:'bold'
            }
        }
    }
    render(){
        const [one,two,three,four,five,six,seven,eight,nine,ten]=["Hussain eranpurwala","Hatim Dahod","Murtaza sidhpurwala","Ahmed Vadnagarwala","Abduttayeb Kanchwala","murtaza Bandeali","Aliasger Kanchwala","Mohammed Bhiwandiwala","Abbas Mamka","Surprise "]
        return (
            <div>
            <div className="categoryName">
                <div onClick={e => this.props.handleClick(one)} className="category" style={this.getStyle(one)}>{one}</div>
                <div onClick={e => this.props.handleClick(two)} className="category" style={this.getStyle(two)}>{two}</div>
                <div onClick={e => this.props.handleClick(three)} className="category" style={this.getStyle(three)}>{three}</div>
                <div onClick={e => this.props.handleClick(four)} className="category" style={this.getStyle(four)}>{four}</div>
            </div>
            <div className="categoryName">
            <div onClick={e => this.props.handleClick(five)} className="category" style={this.getStyle(five)}>{five}</div>
            <div onClick={e => this.props.handleClick(six)} className="category" style={this.getStyle(six)}>{six}</div>
            <div onClick={e => this.props.handleClick(seven)} className="category" style={this.getStyle(seven)}>{seven}</div>
            <div onClick={e => this.props.handleClick(eight)} className="category" style={this.getStyle(eight)}>{eight}</div>
            </div>
            <div className="categoryName">
            <div onClick={e => this.props.handleClick(nine)} className="category" style={this.getStyle(nine)}>{nine}</div>
            <div onClick={e => this.props.handleClick(ten)} className="category" style={this.getStyle(ten)}>{ten}</div>
            </div>
            </div>
        )
    }
}

export default CategoryName