import './App.css';
import React from 'react';
import Nav from './component/Nav'
import Header from './component/Header'
import Bottom from './component/Bottom'
import Carousel from './component/Carousel'
import SpecialOfferings from './component/SpecialOfferings'
import CategoryName from './component/CategoryName'
import Products from './component/Products'
import Data from './Data.js'
import SecondHeading from './component/SecondHeading'
import Reviews from './component/Reviews'
import AboutUs from './component/AboutUs'

class App extends React.Component {
  constructor(){
    super()
    this.state={vsrc:"Hussain eranpurwala",page:1}
    this.handleClick=this.handleClick.bind(this)
    this.handlePage=this.handlePage.bind(this)
  }
  handleClick(id){
    this.setState({vsrc:id})
  }
  handlePage(num){
    this.setState({page:num})
  }
  render(){
    if(window.innerWidth>=700) return (<div>PLease view on a mobile device</div>)
      return (
        <div className="fitToScreen">
          <Header />
          <div className="App">
            <Carousel />
            <SpecialOfferings />
            <CategoryName 
              handleClick={this.handleClick}
              vsrc={this.state.vsrc}
            />
            <Products 
              vsrc={this.state.vsrc} 
            />
            <Bottom />
          </div>
          {/* <Nav handleClick={this.handlePage} page={this.state.page}/> */}
        </div>
      );
  }
}

export default App;
